body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form.new-tip-form {
  margin: 2%;
  padding: 1em;
  display: flex;
  flex-direction: column;
  border-top-left-radius: 10%;
  border-top-right-radius: 10%;
  border-bottom-right-radius: 10%;
  border-bottom-left-radius: 10%;
  background-color: #03042e;
  width: 45vh;
  margin-left: 40px;
  margin-top: 40px;
}
form.new-tip-form input{
  margin: 4%;
  margin-bottom: 2%;
  height: 30px;
}
form.new-tip-form textarea{
  margin: 4%;
}

.btn-inpt{
  width: 40%;
  margin-bottom: 4%;
  padding: 0;
  background-color:white;
  color: black;
  font-size: 1.0rem;
  text-align: center;
  border-top-left-radius: 10%;
  border-top-right-radius: 10%;
  border-bottom-right-radius: 10%;
  border-bottom-left-radius: 10%;
}

.home-div article{
  width: 50%;
  padding: 2%;
}

.navbar{
  font-size: 2.0rem;
  text-align: center;
 
}
nav{
  margin-right: 4%;
}

ul {
  list-style: none;
}

li {
  display: inline;
  margin: 0 5px;
  text-decoration: none;
}
.tech-tips{
  display: flex;
  flex-direction: row;
}
.tips-container{
  width: 60%;
  font-size: 1.5rem;
}
.tips-det{
  padding-right: 4%;
  padding-left: 2%;
  padding-bottom: 2%;
  background-color: white;
}

.tips-det h3{
  text-align: center;
  font-size: 1.5rem;
}
.tips-det h4{
  text-align: center;
}
.tips-det p{
  padding-top: 2%;
}
.tips-det button{
  width: 100px;
  height: 50px;
  background: none;
  border: none;
  text-align: center;
}
.tips-det button img{
  width: 40px;
  align-items: center;
  margin-left: 40vh;
}
.new-tip-div button{
  margin-top: 10%;
  margin-left: 40px;
  background-color: #03042e;
  color: white;
  font-weight: 600;
  padding: 2%;
  width: 50%;
}
.new-tip-div{
  min-height: 100vh;
  min-width: 12em;
  width: 28%;
  padding: 1em;
}

.books-auth-det{
  display: flex;
  flex-direction: row;
}
.book-img img{
  width: 200px;
  height: 200px;
}
.book-item-det{
  margin-left: 4%;
}
.books-container{
  padding: 4%;
  margin-right: 10%;
  display: flex;
  flex-direction: column;
}
.books-det{
  width: 50%;
  background-color: white;
  padding-left: 4%;
  margin-top: 2%;
  padding-bottom: 2%;
}
.news-container{
  padding-right: 10%;
  margin-right: 4%;
}
.news-det{
  background-color: #ffffff;
  padding: 4%;
  margin-top: 2%;
  font-size: 1.5rem;
}
.home-div{
  font-size: calc(10px + 2vmin);
}
.App {
  text-align: justify;
  padding-left: 15%;
  background-color: #f9f4f5
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

